:root {
  --color-black: #191c27;
  --color-white: #ffffff;
  --color-green: #82efa6;
  --color-dark-grey: #32343f;
  --color-light-grey: lightslategrey;
  --spacing: 1rem;
  --text: var(--color-black);
  --text-inverted: var(--color-white);
  --block-fx-shadow: rgba(24, 24, 24, 0.1);
  font-size: 16px;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-kerning: auto;
          font-kerning: auto;
}

html {
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Fira Sans', 'Droid Sans',
    'Helvetica Neue', sans-serif;
  -webkit-text-size-adjust: 100%;
  height: 100%;
}

body {
  margin: 0;
  background-color: var(--color-black);
  color: var(--color-white);
  min-width: 320px;
  min-height: 100%;
  background: var(--color-black);
  background: -webkit-linear-gradient(
    to right,
    #24243e,
    var(--color-black)
  );
  background: -webkit-gradient(linear, left top, right top, from(#24243e), to(var(--color-black)));
  background: -webkit-linear-gradient(left, #24243e, var(--color-black));
  background: -o-linear-gradient(left, #24243e, var(--color-black));
  background: linear-gradient(to right, #24243e, var(--color-black));
}

p {
  font-size: 1.2rem;
  line-height: 1.8rem;
}

footer {
  padding: 2rem;
}

h1 {
  font-size: 1.8rem;
  line-height: 2.4rem;
}

h3 {
  font-size: 1.4rem;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
}

.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.spacer {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}

.fadein {
  -webkit-animation: fadein 0.8s ease-in-out;
          animation: fadein 0.8s ease-in-out;
}

.fade-up {
  -webkit-animation: up 0.6s ease-in, fadein 0.8s ease-in;
          animation: up 0.6s ease-in, fadein 0.8s ease-in;
}

.fade-down {
  -webkit-animation: down 0.6s ease-in, fadein 0.8s ease-in;
          animation: down 0.6s ease-in, fadein 0.8s ease-in;
}

@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes up {
  0% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes up {
  0% {
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@-webkit-keyframes down {
  0% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes down {
  0% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@media screen and (max-width: 840px) {
  .row {
    -ms-flex-direction: column;
        flex-direction: column;
  }

  p {
    font-size: 1rem;
    line-height: 1.5rem;
  }
}

@media screen and (max-width: 380px) {
  :root {
    font-size: 12px;
  }
}


